// Control.js
import React, { useState } from 'react';
import { Checkbox } from 'semantic-ui-react';

function Control() {
  const [isOpen, setIsOpen] = useState(false);

  const handleCheckboxChange = () => {
    //  setIsOpen 
    setIsOpen(!isOpen);
  };

  return (
    <div>
      <h2></h2>
      <Checkbox
        toggle
        label={`: ${isOpen ? '' : ''}`}
        checked={isOpen}
        onChange={handleCheckboxChange}
      />
    </div>
  );
}

export default Control;
